import React, { FC, useEffect } from "react";
import {Profile} from "./Profile";
import {getProfile, getStatus} from "../../redux/profile-reducer";
import { useParams, Navigate, Params } from "react-router-dom";
import { AppStateType } from "../../redux/redux-store";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";

export const ProfilePage: FC = () => {
    let {userId}: Readonly<Params<string>> = useParams()

    const authorizedUserId = useSelector((state: AppStateType) => state.auth.userId)

    const dispatch = useDispatch<any>()
    useEffect(() => {
        if (userId) {
            dispatch(getProfile(Number(userId)))
            dispatch(getStatus(Number(userId)))
        } else throw new Error('userId should exists')
    }, [userId])

    if (!userId) {  
        userId = `${authorizedUserId}`
        if (!authorizedUserId) {
            return <Navigate to={"/login"} />
        }
    }

    return (
        <>
            {!userId && !authorizedUserId
                ? <Navigate to={"/login"} />
                : <Profile />}
        </>
    )
}
