import React, {ChangeEvent, useEffect, useState} from "react";
import { FC } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { Params, useParams } from "react-router-dom";
import { updateStatus } from "../../../../redux/profile-reducer";
import { AppStateType } from "../../../../redux/redux-store";
import s from "./ProfileStatus.module.css"

const ProfileStatus: FC = (props) => {
    const text = useSelector((state: AppStateType) => state.profilePage.status)
    let {userId}: Readonly<Params<string>> = useParams()

    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(text)

    const dispatch = useDispatch<any>()
   

    useEffect(() => {
        setStatus(text)
    }, [text])

    const activateEditMode = () => {
        if (!userId) {
            setEditMode(true)
        }
    }

    const deactivateEditMode = () => {
        setEditMode(false)
        dispatch(updateStatus(status))
    }

    const onStatusChange = (event: ChangeEvent<HTMLInputElement>) => {
        setStatus(event.currentTarget.value)
    }

    return (
        <div>
            <div className={userId ? s.status : undefined}>
                {!editMode &&
                    <div>
                        <b>Status:</b> <span onDoubleClick={activateEditMode}>{text || "Add status"}</span>
                    </div>
                }
                {editMode &&
                    <input onChange={onStatusChange} onBlur={deactivateEditMode} autoFocus={true}
                           defaultValue={status}/>
                }
            </div>
        </div>
    )
}

export default ProfileStatus;